let _this;
export default {
  name: "Login",
  data() {
    return {
      isLoginLoading: false, //是否登陆
      userInfo: {
        username: "",
        password: "",
      },
    }
  },
  mounted() {
    _this = this;
  },
  methods: {
    login: function() {
      _this.$validator.validateAll('userInfo').then(result =>{
        if(result){
          let loginInfo = JSON.parse(JSON.stringify(_this.userInfo));
          loginInfo.password = _this.$md5(loginInfo.password).toLowerCase();
          _this.isLoginLoading = true;
          _this.$ajax.post('/api/AccessToken/Login',loginInfo).then(function (res) {
            if (res.data.success) {
              _this.$root.Authorization = 'Bearer ' + res.data.data.accessToken;
              localStorage.setItem('Authorization',_this.$root.Authorization);
              _this.$root.menu.navTitle = 'M_Report';
              localStorage.setItem('navTitle','M_Report');
              _this.$root.getUserMenu();
              _this.$router.push('/report');
            } else {
              _this.isLoginLoading = false;
              _this.messageNotice("error",res.data.message);
            }
          })
        }
      });
    },
  }
}



